import React, { useEffect, useState } from 'react';
import { Button } from '../Button';
import { Row } from '../Row/Row';

export const Table = ({ title, data, min, max, handleOnSave }) => {
  const [_data, setData] = useState(data);

  useEffect(() => {
    setData(
      _data.map(item => {
        return { ...item, editable: { value: true } };
      }),
    );
  }, []);

  const handleOnAdd = () => {
    if (_data.length < max) {
      setData([
        ..._data,
        { ..._data[_data.length - 1], id: { value: _data.length + 1 }, editable: { value: true } },
      ]);
    } else console.log('That is the max!!!');
  };

  const handleOnDelete = () => {
    if (_data.length > min) {
      setData(_data.filter(item => item.id.value !== _data.length));
    } else console.log('That is the min!!!');
  };

  const onItemChange = item => {
    console.log(item);
    setData(
      _data.map(serie => {
        if (serie.id.value === item.id.value) {
          return item;
        }
        return serie;
      }),
    );
  };

  return (
    <div>
      <div>
        <h2>{title}</h2>
      </div>
      <div>
        <table className="table table-hover">
          <thead>
            <tr>
              {Object.keys(data[0]).map(c => (
                <th key={c} scope="col">
                  {c}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {_data.map(item => (
              <Row key={item.id.value} item={item} onItemChange={onItemChange}></Row>
            ))}
          </tbody>
        </table>
      </div>
      <div class="row">
        <div className="col-sm">
          <Button color="success" onClick={e => handleOnSave(_data)}>
            Save
          </Button>
        </div>
        <div xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </div>
      </div>
    </div>
  );
};
